.react-any-slider-dots {
  position: absolute;
  bottom: 12px;
  left: 50%;
  overflow: hidden;
  transform: translateX(-50%);
}

.react-any-slider-dots__container {
  transition: transform .5s ease-out;
  display: flex;
}

.react-any-slider-dots__dot {
  opacity: .75;
  background-color: #fff;
  border-radius: 50%;
  flex: none;
  transition: transform .5s ease-out;
  transform: scale(.5);
}

.react-any-slider-dots__dot--big {
  transform: scale(1);
}

.react-any-slider-dots__dot--active {
  opacity: 1;
}

